import React from 'react';
import './index.scss'
import { Menu, message, Modal, Button, Form, Input } from 'antd'
const { SubMenu } = Menu;

class ContextMenu extends React.Component {
    constructor(props) {
        super(props);
        this.menuType = null; // add / update
        this.cilckType = null; //  cut/ copy_cur /copy_curAndCild
        this.formRef = React.createRef()
        this.isStartEdit = true // 
        this.state = {
            modalvisible: false, // 
            submitLoading: false, // 
            contextMenuVisible: false, // 
            menuTransform: {
                top: null,
                left: null
            },
            editBtnPopVisible: false,
            isNoEditRole: true
        };
    }
    componentDidMount() {
        if (this.props.onRef)
            this.props.onRef(this);
    }
    UNSAFE_componentWillUpdate(newProps, newState, newContext) {
        // state,
        // 
        if (newState.contextMenuVisible) {
            document.removeEventListener("keydown", this.handleKeyDown);
        } else {
            document.addEventListener("keydown", this.handleKeyDown);
        }
    }
    componentDidUpdate(prevProps, prevState) {
        // state,
        const { modalvisible } = this.state,
            { selectNode } = this.props
        if (!prevState.modalvisible && modalvisible && this.menuType === 'update') {
            this.formRef.current.setFieldsValue(selectNode._node.data)
        }

    }
    // 
    closeMenu = (markClose) => {
        const { onCancelSelectNode } = this.props

        this.setState({ contextMenuVisible: false })
        if (markClose) {
            onCancelSelectNode()
        }
    }
    // 
    handleCreateNode() {
        const { selectNode } = this.props
        if (!selectNode) {
            message.error('');
            return;
        }
        this.menuType = "create"
        this.setState({ modalvisible: true })
    }
    // 
    handleDelete_cur() {
        const { selectNode, onRemoveNode } = this.props
        if (!selectNode) {
            message.error('');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('');
            return;
        }
        const delete_cur_mes = message.warning(<> ? <div style={{ padding: "12px 0 4px" }}><Button size="small" onClick={cancelDeleteNode}> </Button> <Button type="primary" size="small" onClick={confirmDeleteNode}> </Button></div> </>, 0);
        function cancelDeleteNode() {
            setTimeout(delete_cur_mes, 1)
        }
        function confirmDeleteNode() {
            onRemoveNode(selectNode._node)
            setTimeout(delete_cur_mes, 1)
        }
    }
    // 
    handleDelete_curAndCild() {
        const { selectNode, onRemoveNodeAndChildren } = this.props
        if (!selectNode) {
            message.error('');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('');
            return;
        }
        const delete_curAndCild_mes = message.warning(<> ? <div style={{ padding: "12px 0 4px" }}><Button size="small" onClick={cancelDeleteNodes}> </Button> <Button type="primary" size="small" onClick={confirmDeleteNodes}> </Button></div> </>, 0);
        function cancelDeleteNodes() {
            setTimeout(delete_curAndCild_mes, 1)
        }
        function confirmDeleteNodes() {
            onRemoveNodeAndChildren(selectNode._node)
            setTimeout(delete_curAndCild_mes, 1)
        }
    }
    // 
    handleUpdateNode() {
        const { selectNode } = this.props;
        if (!selectNode) {
            message.error('');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('');
            return;
        }
        this.menuType = "update"
        this.setState({ modalvisible: true })
    }
    // 
    handleCutNode() {
        const { selectNode, onCutNode } = this.props,
            _this = this;
        if (!selectNode) {
            message.error('');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('');
            return;
        }
        const cut_mes = message.warning(<> ? <br /> : <div style={{ padding: "12px 0 4px" }}><Button size="small" onClick={cancelCut}> </Button> <Button type="primary" size="small" onClick={confirmCut}> </Button></div> </>, 0);
        function cancelCut() {
            setTimeout(cut_mes, 1)
        }
        function confirmCut() {
            setTimeout(cut_mes, 1)
            onCutNode(selectNode._node, selectNode._dom)
            message.success('!');
            _this.cilckType = "cut"
        }
    }
    // 
    handleCopy_cur() {
        const { selectNode, onTreeSettingChange } = this.props
        if (!selectNode) {
            message.error('');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('');
            return;
        }
        this.cilckType = "copy_cur"
        onTreeSettingChange(selectNode._node, "copyNode")
        message.success('!');
    }
    // 
    handleCopy_curAndCild() {
        const { selectNode, onTreeSettingChange } = this.props
        if (!selectNode) {
            message.error('');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('');
            return;
        }
        onTreeSettingChange(selectNode._node, "copyNode")
        message.success('!');
        this.cilckType = "copy_curAndCild"
    }
    // 
    handlePasteNode() {
        const { selectNode, copyNode, cutNode, onPasteNode, onCutPasteNode, onPasteNodeAndChildren } = this.props

        if (!copyNode && !cutNode.cutNode_node) {
            message.error('/');
            return;
        }
        if (this.cilckType === "copy_cur") {
            onPasteNode(selectNode._node, copyNode)
        } else if (this.cilckType === "copy_curAndCild") {
            onPasteNodeAndChildren(selectNode._node, copyNode)
        } else if (this.cilckType === "cut") {
            onCutPasteNode(selectNode._node, cutNode)
        }
    }
    // 
    handleClick = (e) => {
        this.closeMenu()
        if (this.isStartEdit) {
            switch (e.key) {
                // 
                case 'create':
                    this.handleCreateNode()
                    break;
                // 
                case 'delete_cur':
                    this.handleDelete_cur()
                    break;
                // 
                case 'delete_curAndCild':
                    this.handleDelete_curAndCild()
                    break;
                // 
                case 'update':
                    this.handleUpdateNode()
                    break;
                // 
                case 'cut':
                    this.handleCutNode()
                    break;
                // 
                case 'copy_cur':
                    this.handleCopy_cur()
                    break;
                // 
                case 'copy_curAndCild':
                    this.handleCopy_curAndCild()
                    break;
                // 
                case 'paste':
                    this.handlePasteNode()
                    break;
                default:
                    break;
            }
        } else {
            message.error("")
        }
    }
    // 
    handleKeyDown = (e) => {
        if (e.ctrlKey && [65, 75, 77, 88, 67, 86, 90].indexOf(e.keyCode) !== -1) {
            e.preventDefault()

            if (this.isStartEdit) {
                switch (e.keyCode) {
                    case 65:
                        // ctrl+a 
                        this.handleCreateNode()
                        break;
                    case 75:
                        // ctrl+k 
                        this.handleDelete_cur()
                        break;
                    case 77:
                        // ctrl +m 
                        this.handleUpdateNode()
                        break;
                    case 88:
                        // ctrl+x 
                        this.handleCutNode()
                        break;
                    case 67:
                        // ctrl+c 
                        this.handleCopy_cur()
                        break;
                    case 86:
                        // ctrl+v 
                        this.handlePasteNode()
                        break;
                    case 90:
                        // ctrl+z 
                        this.props.onUndo()
                        break;
                    default:
                        break;
                }
            } else {
                message.error("")
            }
        }
    }

    // 
    handleCancel = () => {
        this.setState({ submitLoading: false, modalvisible: false })
        this.onReset()
    }
    // 
    onAddFinish = (newNodeInfo) => {
        let _this = this
        const { selectNode } = _this.props
        _this.setState({ submitLoading: true })
        const node = {
            name: newNodeInfo.name,
            description: newNodeInfo.description
        }
        this.props.onAddNode(selectNode._node, node).then(info => {
            if (info) {
                _this.setState({ submitLoading: false, modalvisible: false })
                _this.onReset()
                message.success('!');
            }

        })
    };
    // 
    onUpdateFinish = (newNodeInfo) => {
        let _this = this
        const { selectNode } = _this.props
        _this.setState({ submitLoading: true })
        this.props.onUpdateNode(newNodeInfo, selectNode._node).then(info => {
            if (info) {
                _this.setState({ submitLoading: false, modalvisible: false })
                _this.onReset()
                message.success('!');
            } else {
                message.error('!');
            }

        })
    };
    // 
    onReset = () => {
        this.formRef.current.resetFields();
    };
    render() {
        //editBtnPopVisible
        const { modalvisible, submitLoading, contextMenuVisible, menuTransform } = this.state
        const { copyNode, cutNode } = this.props
        return (
            <>
                <div className="menu" style={contextMenuVisible ? { display: 'block', top: menuTransform.top, left: menuTransform.left } : { display: 'none' }}>
                    <div className="menu-mask" onClick={() => { this.closeMenu(1); }}></div>
                    <Menu className="menu-box" onClick={this.handleClick} selectable={false}>
                        <Menu.Item className="menu-item" key="create"  > (ctrl+a)</Menu.Item>
                        <SubMenu key="delete" popupClassName="menu-item" title="">
                            <Menu.Item className="subMenu-item" key="delete_cur"> (ctrl+k)</Menu.Item>
                            <Menu.Item className="subMenu-item" key="delete_curAndCild"></Menu.Item>
                        </SubMenu>
                        <Menu.Item className="menu-item menu-divider" key="update"  > (ctrl+m)</Menu.Item>
                        <Menu.Item className="menu-item" key="cut"  > (ctrl+x)</Menu.Item>
                        <SubMenu key="copy" popupClassName="menu-item" title="" >
                            <Menu.Item className="subMenu-item" key="copy_cur"> (ctrl+c)</Menu.Item>
                            <Menu.Item className="subMenu-item" key="copy_curAndCild"></Menu.Item>
                        </SubMenu>
                        <Menu.Item className="menu-item" key="paste" disabled={copyNode || cutNode.cutNode_node ? false : true}  > (ctrl+v)</Menu.Item>
                    </Menu>
                </div>
                <Modal
                    title={this.menuType === "create" ? "" : ""}
                    visible={modalvisible}
                    onCancel={this.handleCancel}
                    footer={null}
                >
                    <Form
                        name="basic"
                        labelCol={{
                            xs: { span: 24 },
                            sm: { span: 6 },
                        }}
                        wrapperCol={{
                            xs: { span: 24 },
                            sm: { span: 16 },
                        }}
                        ref={this.formRef}
                        onFinish={this.menuType === "create" ? this.onAddFinish : this.onUpdateFinish}
                    >
                        <Form.Item
                            label=""
                            name="name"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input autoComplete="off" />
                        </Form.Item>
                        <Form.Item name="description" label="">
                            <Input.TextArea rows={4} allowClear />
                        </Form.Item>
                        <Form.Item wrapperCol={{ offset: 6, span: 16 }}>
                            <Button type="primary" htmlType="submit" style={{ marginRight: '8px' }} loading={submitLoading}>
                                
                            </Button>
                            <Button htmlType="button" onClick={this.onReset}>
                                
                            </Button>
                        </Form.Item>
                    </Form>
                </Modal>
            </>
        )
    }
}

export default ContextMenu;


